import './Input.css'
function TodoItems(props){
    const Delete=(key)=>{
        props.delete(key)
    }
    return(
       
        <>
            <ul className="theList">
            {
                props.entries.map((item)=>{
                    return <li key={item.key} onClick={()=>Delete(item.key)}>{item.text} </li>
                })
            }
            </ul>
        </>
    )

}
export default TodoItems